
.datepicker .picker-box-bg {
  background: rgba(0,0,0,0.35);
  z-index: 9998;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.datepicker .picker-box {
  position: fixed;
  z-index: 9999;
  background: #fff;
  bottom: 0;
  left: 0;
  right: 0;
}
.datepicker .picker-box-header {
  background-color: #eee;
  height: 1.066666666666667rem;
  line-height: 1.066666666666667rem;
  overflow: hidden;
  text-align: center;
  font-size: 18px;
}
.datepicker .picker-box-header-cancle {
  float: left;
  padding: 0 0.266666666666667rem;
  color: #078ffd;
  font-size: 18px;
}
.datepicker .picker-box-header-confirm {
  float: right;
  padding: 0 0.266666666666667rem;
  color: #ff5657;
  font-size: 18px;
}
.datepicker .picker-box-content {
  width: 100%;
  margin: 0 auto;
  background: #fff;
  overflow: hidden;
  height: 6rem;
  overflow: hidden;
}
.datepicker .picker-box-content-list {
  float: left;
  width: 20%;
  text-align: center;
}
.datepicker .picker-box-content-list ul {
  margin: 0;
  padding: 0;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.datepicker .picker-box-content-list ul.is_dragging {
  -webkit-transition: none;
  transition: none;
}
.datepicker .picker-box-content-list li {
  margin: 0;
  padding: 0;
  line-height: 0.853333333333333rem;
  height: 0.853333333333333rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 14px;
}
.datepicker .picker-box-content-list li.current {
  font-size: 18px;
  font-weight: normal;
}
.datepicker .picker-box-content-list li.node1 {
  font-size: 16px;
  opacity: 0.7;
}
.datepicker .picker-box-content-list li.node2 {
  font-size: 14px;
  opacity: 0.5;
}
.datepicker .picker-box-content-list li.node3 {
  font-size: 14px;
  opacity: 0.3;
}
.datepicker .selected-top-line {
  width: 100%;
  border: none;
  border-top: 1px solid #eee;
  position: absolute;
  bottom: 3.333333333333333rem;
  margin: 0;
  height: 0;
}
.datepicker .selected-bottom-line {
  width: 100%;
  border: none;
  border-top: 1px solid #eee;
  position: absolute;
  bottom: 2.613333333333333rem;
  margin: 0;
  height: 0;
}
.datepicker .fade-enter-active,
.datepicker .fade-leave-active {
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.datepicker .fade-enter,
.datepicker .fade-leave-active {
  opacity: 0;
}
.datepicker .select-enter-active,
.datepicker .select-leave-active {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.datepicker .select-enter,
.datepicker .select-leave-active {
  -webkit-transform: translate(0, 7.066666666666666rem);
          transform: translate(0, 7.066666666666666rem);
}